'use client';

import { useState } from 'react';
import ChatSidebar from './components/ChatSidebar';
import { Todo } from './utils/instructionMapper';
import { useInstructionMapping } from './hooks/useInstructionMapping';

export default function TodoList() {
  const [todos, setTodos] = useState<Todo[]>([]);
  const [inputValue, setInputValue] = useState('');

  // ‰ΩøÁî®Êåá‰ª§Êò†Â∞Ñhook
  const { 
    executeInstruction, 
    getSupportedActions,
    addTodo: addTodoFunction,
    clearCompleted: clearCompletedFunction,
    clearAll: clearAllFunction
  } = useInstructionMapping({ todos, setTodos });

  // Êú¨Âú∞Ê∑ªÂä†‰ªªÂä°ÔºàÈÄöËøáUIÁõ¥Êé•Êìç‰ΩúÔºâ
  const addTodo = () => {
    if (inputValue.trim() !== '') {
      const result = addTodoFunction(inputValue.trim());
      if (result.success) {
        setInputValue('');
      } else {
        console.error('Ê∑ªÂä†‰ªªÂä°Â§±Ë¥•:', result.message);
      }
    }
  };

  // Êú¨Âú∞Âà†Èô§‰ªªÂä°ÔºàÈÄöËøáUIÁõ¥Êé•Êìç‰ΩúÔºâ
  const deleteTodo = (id: number) => {
    const result = executeInstruction({
      action: 'delete',
      id: id
    });
    if (!result.success) {
      console.error('Âà†Èô§‰ªªÂä°Â§±Ë¥•:', result.message);
    }
  };

  // Êú¨Âú∞ÂàáÊç¢‰ªªÂä°Áä∂ÊÄÅÔºàÈÄöËøáUIÁõ¥Êé•Êìç‰ΩúÔºâ
  const toggleTodo = (id: number) => {
    const todo = todos.find(t => t.id === id);
    if (todo && !todo.completed) {
      const result = executeInstruction({
        action: 'complete',
        id: id
      });
      if (!result.success) {
        console.error('ÂÆåÊàê‰ªªÂä°Â§±Ë¥•:', result.message);
      }
    } else if (todo && todo.completed) {
      // ÂØπ‰∫éÂ∑≤ÂÆåÊàêÁöÑ‰ªªÂä°ÔºåÊàë‰ª¨ÂèØ‰ª•ÂèñÊ∂àÂÆåÊàêÁä∂ÊÄÅ
      setTodos(todos.map(t =>
        t.id === id ? { ...t, completed: false } : t
      ));
    }
  };

  // ÈÄöËøáÊò†Â∞ÑÁ≥ªÁªüÊ∏ÖÈô§Â∑≤ÂÆåÊàê‰ªªÂä°
  const clearCompleted = () => {
    const result = clearCompletedFunction();
    if (!result.success) {
      console.error('Ê∏ÖÈô§Â∑≤ÂÆåÊàê‰ªªÂä°Â§±Ë¥•:', result.message);
    }
  };

  // ÈÄöËøáÊò†Â∞ÑÁ≥ªÁªüÊ∏ÖÈô§ÊâÄÊúâ‰ªªÂä°
  const clearAll = () => {
    const result = clearAllFunction();
    if (!result.success) {
      console.error('Ê∏ÖÈô§ÊâÄÊúâ‰ªªÂä°Â§±Ë¥•:', result.message);
    }
  };

  const completedCount = todos.filter(todo => todo.completed).length;
  const totalCount = todos.length;

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 dark:from-gray-900 dark:to-gray-800 p-4">
      <div className="max-w-6xl mx-auto h-[calc(100vh-2rem)] flex flex-col lg:flex-row gap-6">
        {/* Â∑¶‰æßÔºöÂæÖÂäû‰∫ãÈ°π */}
        <div className="flex-1 bg-white dark:bg-gray-800 rounded-xl shadow-lg p-6 overflow-y-auto">
          <div className="mb-4">
            <h1 className="text-3xl font-bold text-center text-gray-800 dark:text-white mb-2">
              üìù Êô∫ËÉΩÊåá‰ª§Êò†Â∞ÑÂæÖÂäû‰∫ãÈ°π
            </h1>
            <p className="text-center text-sm text-gray-600 dark:text-gray-400">
              AI Êåá‰ª§Â∞ÜÊò†Â∞ÑÂà∞Êú¨Âú∞ÂáΩÊï∞ÊâßË°å | ÊîØÊåÅÊìç‰Ωú: {getSupportedActions().join(', ')}
            </p>
          </div>
          
          {/* Ê∑ªÂä†Êñ∞‰ªªÂä° */}
          <div className="flex gap-2 mb-6">
            <input
              type="text"
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && addTodo()}
              placeholder="Ê∑ªÂä†Êñ∞‰ªªÂä°..."
              className="flex-1 px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-gray-700 dark:text-white"
            />
            <button
              onClick={addTodo}
              className="px-6 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors duration-200 font-medium"
            >
              Ê∑ªÂä†
            </button>
          </div>

          {/* ÁªüËÆ°‰ø°ÊÅØ */}
          {totalCount > 0 && (
            <div className="mb-4 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
              <p className="text-sm text-gray-600 dark:text-gray-300 text-center">
                ÊÄªËÆ°: {totalCount} ‰ªªÂä° | Â∑≤ÂÆåÊàê: {completedCount} ‰ªªÂä° | 
                Ââ©‰Ωô: {totalCount - completedCount} ‰ªªÂä°
              </p>
            </div>
          )}

          {/* ‰ªªÂä°ÂàóË°® */}
          <div className="space-y-2">
            {todos.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-gray-500 dark:text-gray-400 text-lg">
                  ÊöÇÊó†‰ªªÂä°ÔºåÊ∑ªÂä†‰∏Ä‰∏™Êñ∞‰ªªÂä°ÂºÄÂßãÂêßÔºÅ
                </p>
                <p className="text-sm text-gray-400 dark:text-gray-500 mt-2">
                  üí° ‰Ω†ÂèØ‰ª•Áõ¥Êé•Âú®Ê≠§Ê∑ªÂä†ÔºåÊàñÂêë<span className="lg:inline hidden">Âè≥‰æß</span><span className="lg:hidden inline">‰∏ãÊñπ</span>ÁöÑ AI Âä©ÊâãÂèëÈÄÅÊåá‰ª§
                </p>
                <p className="text-xs text-gray-400 dark:text-gray-500 mt-1">
                  ‰æãÂ¶ÇÔºö&ldquo;Â∏ÆÊàëÊ∑ªÂä†‰∏Ä‰∏™Â≠¶‰π†‰ªªÂä°&rdquo; Êàñ &ldquo;Ê∑ªÂä†‰π∞Ëèú‰ªªÂä°&rdquo;
                </p>
              </div>
            ) : (
              todos.map((todo) => (
                <div
                  key={todo.id}
                  className={`flex items-center gap-3 p-3 rounded-lg border transition-all duration-200 ${
                    todo.completed
                      ? 'bg-green-50 dark:bg-green-900/20 border-green-200 dark:border-green-800'
                      : 'bg-white dark:bg-gray-700 border-gray-200 dark:border-gray-600 hover:shadow-md'
                  }`}
                >
                  <input
                    type="checkbox"
                    checked={todo.completed}
                    onChange={() => toggleTodo(todo.id)}
                    className="w-5 h-5 text-blue-600 rounded focus:ring-blue-500 focus:ring-2"
                  />
                  <span
                    className={`flex-1 ${
                      todo.completed
                        ? 'line-through text-gray-500 dark:text-gray-400'
                        : 'text-gray-800 dark:text-white'
                    }`}
                  >
                    {todo.text}
                  </span>
                  <span className="text-xs text-gray-400 dark:text-gray-500">
                    ID: {todo.id}
                  </span>
                  <button
                    onClick={() => deleteTodo(todo.id)}
                    className="px-3 py-1 text-red-600 hover:bg-red-100 dark:hover:bg-red-900/30 rounded transition-colors duration-200"
                    title="Âà†Èô§‰ªªÂä°"
                  >
                    üóëÔ∏è
                  </button>
                </div>
              ))
            )}
          </div>

          {/* Âø´Êç∑Êìç‰Ωú */}
          {todos.length > 0 && (
            <div className="mt-6 flex gap-2 justify-center">
              <button
                onClick={clearCompleted}
                className="px-4 py-2 text-sm bg-red-100 hover:bg-red-200 text-red-700 rounded-lg transition-colors duration-200"
                disabled={completedCount === 0}
              >
                Ê∏ÖÈô§Â∑≤ÂÆåÊàê ({completedCount})
              </button>
              <button
                onClick={clearAll}
                className="px-4 py-2 text-sm bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg transition-colors duration-200"
              >
                Ê∏ÖÈô§ÂÖ®ÈÉ®
              </button>
            </div>
          )}
        </div>

        {/* Âè≥‰æßÔºöAIÂä©Êâã‰æßËæπÊ†è */}
        <div className="w-full lg:w-80 h-96 lg:h-full">
          <ChatSidebar executeInstruction={executeInstruction} />
        </div>
      </div>
    </div>
  );
}
